nav{
    background:rgb(0,0,0,0.2) ;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem  ;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.9rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);

}


nav a{
    background: transparent;
    padding: 0.8rem;
    border-radius:  50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.2rem;

}


nav a:hover{
    background: rgb(0, 0,0,0.4);
}


nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);

}

